import moment from 'moment';
import XLSX from 'xlsx';


// 无法选择今天以前的日期
export const disabledEndDate = (endValue) => {
    const currentTime = moment();
    const startValue = currentTime;
    if (!endValue || !startValue) {
        return false;
    }
    return endValue.valueOf() >= startValue.valueOf();
}
export const deepCopy=(obj)=>{
    let result = Array.isArray(obj) ? [] : {};
    for (let key in obj) {
      if (obj.hasOwnProperty(key)) {
        if (typeof obj[key] === 'object' && obj[key]!==null) {
          result[key] = deepCopy(obj[key]);   //递归复制
        } else {
          result[key] = obj[key];
        }
      }
    }
    return result;
  }
// 导出table
function exportExcel(sheetList, fileName) {
    const wb = {
        SheetNames: [],
        Sheets: {},
    };
    const sheetArr = JSON.parse(JSON.stringify(sheetList));
    console.log(sheetList,'list')
    sheetArr.forEach((sheet, index) => {
        // console.log(sheet);
        // 删除序号列
        /* if (index === 0) {
            sheet.headers.shift()
            // sheet.headers.shift();
        } */
        // 删除操作列
        if (sheet.headers.find(item => item.title === "操作")) {
            sheet.headers.pop()
        }
        let mergeIndex = 0;
        sheet['header' + index] = sheet.headers
            .map((item, i) => Object.assign({}, {
                key: item.key,
                title: item.title,
                position: String.fromCharCode(65 + i) + 1
            }))
            .reduce((prev, next) => Object.assign({}, prev, {[next.position]: {key: next.key, v: next.title}}), {});

        sheet['data' + index] = sheet.data
            .map((item, i) => sheet.headers.map((key, j) => {
                    // console.log(item,key, 'item');
                    if ((!item.id) && item.total) {
                        // console.log(j);
                        // console.log(i);
                        mergeIndex = i + 1
                    }
                    // console.log(key, 'key');
                    return Object.assign({}, {
                        content: key.key && key.title == '序号'?item[key.key]:item[key.dataIndex],
                        position: String.fromCharCode(65 + j) + (i + 2)
                    })
                }
            ))
            // 对刚才的结果进行降维处理（二维数组变成一维数组）
            .reduce((prev, next) => prev.concat(next))
            // 转换成 worksheet 需要的结构
            .reduce((prev, next) => Object.assign({}, prev, {[next.position]: {v: next.content}}), {});
        // 合并 headers 和 data
        sheet['output' + index] = Object.assign({}, sheet['header' + index], sheet['data' + index]);
        // 获取所有单元格的位置
        sheet['outPos' + index] = Object.keys(sheet['output' + index]);
        // 计算出范围 ,["A1",..., "H2"]
        sheet['ref' + index] = `${sheet['outPos' + index][0]}:${sheet['outPos' + index][sheet['outPos' + index].length - 1]}`;

        wb.SheetNames.push(sheet.sheetName);
        if (mergeIndex) {
            wb.Sheets[sheet.sheetName] = Object.assign(
                {},
                sheet['output' + index],
                {
                    '!ref': sheet['ref' + index],
                    '!cols': [{wpx: 100}, {wpx: 100}, {wpx: 100}, {wpx: 100}, {wpx: 100}, {wpx: 100}, {wpx: 100}, {wpx: 100}, {wpx: 100}, {wpx: 100}, {wpx: 100}, {wpx: 100}, {wpx: 100}, {wpx: 100}, {wpx: 100}],

                    '!merges': [
                        {//合并第一行数据[B1,C1,D1,E1]
                            s: {//s为开始
                                c: 0,//开始列
                                r: mergeIndex //开始取值范围
                            }, e: {//e结束
                                c: 2,//结束列
                                r: mergeIndex //结束范围
                            }
                        }
                    ],
                    ['A' + (mergeIndex + 1)]: {"v": "合计"}
                },
            )
        } else {
            wb.Sheets[sheet.sheetName] = Object.assign(
                {},
                sheet['output' + index],
                {
                    '!ref': sheet['ref' + index],
                    '!cols': [{wpx: 100}, {wpx: 100}, {wpx: 200}, {wpx: 100}, {wpx: 100}, {wpx: 100}, {wpx: 100}, {wpx: 100}],
                },
            )
        }

    })
    // // 导出 Excel
    XLSX.writeFile(wb, fileName);
}

export default exportExcel;