/**
 * 总进度
 */
import React, {PureComponent} from "react";
import {connect} from "react-redux";
import {Button, Form, Select, Table} from 'antd';
import exportExcel from '../../../utils';
import {Layout} from "./style";
import {getTotalProgress} from "../../../axios";
import AuthComponent from "../../common/authComponent ";
import LineWrap from '../../common/lineWrap';
import ResizeableTitle from "../../common/resizable";
const AuthButton = AuthComponent(Button);
const {Option} = Select;
const FormItem = Form.Item;


class AllProgress extends PureComponent {
    state = {
        page: 1,
        size: 20,
        totalPage: 1,
        totalCount:0,
        totalList: [],
        month: 1,
        loading:false,
        columns : [
            {
                title: '序号',
                 width: 50,
                key: 'index',
                render: (text, record, index) => index + 1
            }, {
                title: '时间',
                 width: 200,
                dataIndex: 'label',
                render:(text) => {
                    // console.log(text)
                    if(text.length == 4) {
                        text = `${text}年`
                    } else if(text.indexOf('季')!==-1){
                         
                    } else {
                        text = `${text.substr(0,4)}年${text.substr(text.length-2)}月`;
                    }
                    return <LineWrap title={text}/>
                }
            }, {
                title: '计划量（片）',
                dataIndex: 'y',
                 width: 200,
                 render:(text) => {
                    return <LineWrap title={text}/>
                }
            }, {
                title: '实际量（片）',
                dataIndex: 'z',
                 width: 200,
                 render:(text) => {
                    return <LineWrap title={text}/>
                }
            }, {
                title: '完成率（%）',
                dataIndex: 's',
                width: 200,
                // render: (text) => text.toFixed(2)
                render:(text) => {
                    return <LineWrap title={text.toFixed(2)}/>
                }
            }
        ]
    }

    getTotalProgress() {

        this.setState({
            loading:true
        })
        const {page, size, month} = this.state;
        const pageParams = {
            page, size
        }
        getTotalProgress(pageParams, month).then(res => {
            const responseData = res.data;
            this.setState({
                totalPage: responseData.totalPage,
                totalCount:responseData.totalCount,
                totalList: responseData.items,
                loading:false
            });
        })
    }

    // 筛选月份
    handleChange = (value) => {
        this.setState({
            month: value
        }, () => {
            this.getTotalProgress();
        });
    }

    // 导出
    export(columns, sectionList) {
        let sectionListCopy = JSON.parse(JSON.stringify(sectionList));
        let sectionLists = [];
        sectionListCopy.map((val,key)=>{
            val.s  = val.s.toFixed(2);
            if(val.label.length == 4) {
                val.label = `${val.label}年`
            } else if(val.label.indexOf('季')!==-1){
                 
            } else {
                val.label = `${val.label.substr(0,4)}年${val.label.substr(val.label.length-2)}月`;
            }
            sectionLists.push({index:(key+1),...val})
        })
        const sheetList = [
            {
                headers: columns,
                data: sectionLists,
                sheetName: '总进度'
            }
        ]
        // console.log(sectionLists,'sectionList    ')
        exportExcel(sheetList, '总进度.xlsx')
    }

    components = {
        header: {
            cell: ResizeableTitle,
        },
    };
    handleResize = index => (e, { size }) => {
        this.setState(({ columns }) => {
          const nextColumns = [...columns];
          nextColumns[index] = {
            ...nextColumns[index],
            width: size.width,
          };
          return { columns: nextColumns };
        });
    };
    render() {
        const {
            page, size, totalPage,totalCount, totalList, loading
        } = this.state;
        const beamInfo=this.props.beamInfo.toJS();
        const authCode=this.props.authCode.toJS();
        const columns = this.state.columns.map((col, index) => ({
            ...col,
            onHeaderCell: column => ({
              width: column.width,
              onResize: this.handleResize(index),
            }),
        }));
        return (
            <Layout>
                <div className='layout-header'>
                    <AuthButton source="fun" authcode="77002001" authcodes = {authCode.funAuthCode} type="primary" onClick={() => this.export(columns, totalList)} authcode='77002001' {...beamInfo}>导出</AuthButton>
                    <Form
                        layout="inline"
                        style={{
                            float: 'right',
                            marginTop: '-7px'
                        }}>
                        <FormItem label="筛选">
                            <Select
                                onChange={this.handleChange}
                                className="selectTime"
                                defaultValue={"月"}
                            >
                                <Option value="1">月</Option>
                                <Option value="2">季</Option>
                                <Option value="3">年</Option>
                            </Select>
                        </FormItem>
                    </Form>
                </div>
                <Table
                    rowKey={(record, index) => `complete${index}`}
                    columns={columns}
                    dataSource={totalList}
                    bordered={true}
                    loading={loading}
                    scroll={{y: 600}}
                    components={this.components}
                    pagination={{  // 分页
                        current: page,
                        pageSize: size,
                        total: totalCount,
                        showSizeChanger: true,
                        showQuickJumper: true,
                        pageSizeOptions: ['20', '30', '50'],
                        onChange: this.changePage,
                        showTotal: total => {
                            return `共${totalCount}条`;
                        },
                        onShowSizeChange: this.sizeChange
                    }}/>
            </Layout>
        )
    }

    componentDidMount() {
        this.getTotalProgress();
    }

    sizeChange=(current, pageSize)=> {
        console.log(current, pageSize);
        this.setState({
            page: current,
            size: pageSize,
        }, () => {
            this.getTotalProgress();
        });
    }

    changePage = (page, size) => {
        this.setState({
            page,
            size
        }, () => {
            this.getTotalProgress();
        });
    }
}
const mapStateToProps = state => {
    return {
        beamInfo: state.getIn(["login", "beamInfo"]),
        authCode: state.getIn(["login", "authCode"]),
    };
};
export default connect(
    mapStateToProps,
    null
)(AllProgress);